.App {
  display: flex;
  text-align: center;
  width: 100vw;
  height: 100vh;
  background-color: #adadad;
}

.background {
  position: absolute;
  left:0;
  top:0;
  width: 100vw;
  height: 100vh;
  background-image: url(https://images.unsplash.com/photo-1551269901-5c5e14c25df7?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2250&q=80);
  background-size: cover;
  opacity: 0.5;
  filter: blur(5px);
  z-index: 1;
}

.inputForm {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  width: 40vw;
  height: 100vh;
  z-index: 2;
}

.textBox {
  text-align: left;
  width: 416px;
  height: 40%;
  box-shadow: 2px 2px 5px 1px #1b1b1b7a;
  z-index: 2;
}

.dropImages {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffcd42;
  width: 416px;
  height: 40%;
  border-radius: 5px;
  box-shadow: 2px 2px 5px 1px #1b1b1b7a;
  z-index: 2;
}

.result {
  display: flex;
  background-color: white;
  width: 60vw;
  height: 100vh;
  z-index: 10;
  overflow: auto;
  justify-content: center;
}

.copyText {
  margin: 0;
  padding: 0;
  background-color: #b3e4ff;
  cursor: pointer;
  transition: all .1s ease-in-out;
}

.copyText:hover {
  background-color: #adffa6;
}

.copyText:active {
  color: #ffffff;
  background-color: #ffffff;
}

@media screen and (max-width: 1300px) { 
  .inputForm {
    width: 100vw;
  }
  .result {
    display: none;
  }
}