.frame {
    width: 780px;
    height: max-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    flex-direction: column;
    font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
    z-index: 3;
}
  
  h1 {
    display: inline-block;
    font-size: 20px;
    z-index: 3;
  }
  
  .ItemName {
    display: block;
    width: 560px;
    text-align: left;
    font-size: 35px;
    font-weight: 400;
    color: #333333;
    margin-bottom: 10px;
    z-index: 3;
  }
  
  .Title {
    width: 560px;
    height: 560px;
    display: block;
    overflow: hidden;
    border: 10px solid #cdcdcd;
    z-index: 3;
  }
  
  .Option {
    display: block;
    width: 560px;
    text-align: left;
    z-index: 3;
  }
  
  .opt {
    display: block;
    width: 560px;
    margin-top: 10px;
    z-index: 3;
  }
  
  .option_nm {
    display: inline-block;
    color: #242424;
    width: 130px;
    font-size: 34px;
    font-weight: bold;
    z-index: 3;
  }
  
  .option_val {
    display: inline-block;
    margin-left: 20px;
    color: #242424;
    font-size: 25px;
    font-weight: light;
    z-index: 3;
  }
  
  .item {
      display: table-row;
    margin-top: 30px;
    margin-bottom: 30px;
    z-index: 3;
  }
  
  .Images {
    display: table;
    width: 740px;
    min-height: 500px;
    z-index: 3;
  }

  .sizeTable {
    margin: 0 auto; 
    border-collapse: collapse;
    border : 1px solid black;
}

th {
    width: 150px;
    height: 50px;
    font-size: 14pt; 
    border : 1px solid black;
    border-collapse: collapse;
}

.head {
    background-color: rgb(105, 105, 105);
    color: white;
    font-weight: bold;
}

tr:nth-child(even) {
    background-color: #f2f2f2;
}

.unit {
    margin: 0;
    padding: 10px;
    text-align: end;
    font-size: 20px;
    font-weight: bold;
}

.rocketData {
    font-family: 'Malgun Gothic';
}